<template>

  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        Общее количество наименований товара - {{countProducts}}
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
          :price-from.sync="filterPriceFrom"
          :price-to.sync="filterPriceTo"
          :category-id.sync="filterCategoryId"
          :category-color.sync="filterCategoryColor"
      />
      <section class="catalog">

        <div v-if="productLoading">Загрузка товаров...</div>
        <div v-if="productLoadingFailed">
          Ошибка при загрузке товаров. Что-то пошло не так
          <button @click="loadProducts()">Попробовать еще раз</button>
        </div>

        <ProductList
            :products="products"
        />

        <BasePagination
            v-model="page"
            :count="countProducts"
            :per-page="productsPerPage"
        />

      </section>
    </div>
  </main>

</template>

<script>
import BasePagination from "@/components/BasePagination";
import ProductFilter from "@/components/ProductFilter";
import ProductList from "@/components/ProductList";
import axios from "axios";
import {API_BASE_URL} from "@/config";

export default {
  name: "MainPage",
  components: {ProductFilter, BasePagination, ProductList},
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterCategoryColor: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,


      productLoading: false,
      productLoadingFailed: false,
    }
  },
  computed: {

      products() {
        return this.productsData
            ? this.productsData.items.map(product => {
              // console.log(product)
              return {
                ...product,
                image: product.image.file.url
              }
            })
            : [];
      },
      countProducts() {
        return this.productsData ? this.productsData.pagination.total : 0;
      }
  },
  methods: {
    loadProducts() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
            .get(API_BASE_URL +'/api/products', {
              params: {
                page: this.page,
                limit: this.productsPerPage,
                categoryId: this.filterCategoryId,
                minPrice: this.filterPriceFrom,
                maxPrice: this.filterPriceTo,
                colorId: this.filterCategoryColor
              }
            })
            .then(response => this.productsData = response.data)
            .catch(() => this.productLoadingFailed = true)
            .then(() => this.productLoading = false)
      }, 1000)
    }
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterCategoryColor() {
      this.loadProducts();
    }
  },
  created() {
    this.loadProducts();
  }
}
</script>

