<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        Общее количество товаров {{ totalAmount }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText
                title="ФИО"
                placeholder="Введите ваше полное имя"
                v-model="formData.name"
                :error="formError.name"
                type="text"
            />

            <BaseFormText
                title="Адрес доставки"
                placeholder="Введите ваш адрес"
                v-model="formData.address"
                :error="formError.address"
                type="text"
            />

            <BaseFormText
                title="Телефон"
                placeholder="Введите ваш телефон"
                v-model="formData.phone"
                :error="formError.phone"
                type="tel"
            />

            <BaseFormText
                title="Email"
                placeholder="Введите ваш Email"
                v-model="formData.email"
                :error="formError.email"
                type="email"
            />

            <BaseFormTextarea
                title="Комментарий к заказу"
                placeholder="Ваши пожелания"
                v-model="formData.comment"
                :error="formError.comment"
            />

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderPageItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ totalAmount }}</b> товара(ов) на сумму <b>{{totalPrice | numberFormat}} ₽</b></p>
          </div>

          <button v-show="!flag" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
          <div v-show="flag" class="loader" type="submit">
            <img src="../assets/816.gif" alt="">
          </div>
        </div>
        <div class="cart__error form__error-block" v-show="!flag" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>

</template>

<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import axios from "axios";
import {API_BASE_URL} from "@/config";
import OrderPageItem from "@/components/OrderPageItem";
import {mapGetters} from "vuex";
import numberFormat from "@/helpers/numberFormat";

export default {
  name: "OrderPage",
  components: {OrderPageItem, BaseFormTextarea, BaseFormText},
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      flag: false,
    }
  },
  filters: {numberFormat},
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      totalAmount: 'cartTotalAmount'
    }),

  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = {};
      this.flag = true,

          setTimeout(() => {
            axios
                .post(API_BASE_URL + '/api/orders', {
                  ...this.formData
                }, {
                  params: {
                    userAccessKey: this.$store.state.userAccessKey
                  }
                })
                .then(response => {
                  this.flag = false;
                  this.$store.commit('resetCart');
                  this.$store.commit('updateOrderInfo', response.data);
                  this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
                })
                .catch(error => {
                  this.formError = error.response.data.error.request || {};
                  this.formErrorMessage = error.response.data.error.message;
                })
          }, 2000)

    }
  },
}
</script>

<style scoped>
.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
</style>
