<template>
  <div class="form__counter">
    <button class="btn-counter" @click="validityСontrol()" type="button" aria-label="Убрать один товар">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text"
       :value="productAmount"
      @input="$emit('productCounter', Number($event.target.value))"
    >

    <button class="btn-counter" @click="$emit('productCounter', productAmount+1)" type="button" aria-label="Добавить один товар">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: "ProductCounter",
  props: ['productAmount'],
  methods: {

    validityСontrol() {
      if(this.productAmount > 1) {
        this.$emit('productCounter', this.productAmount-1)
      }
    }
  }
}
</script>

<style scoped>
.form__counter {
  border: 1px solid #ccc;
}
.product__del,
.btn-counter {
  cursor: pointer;
}
.btn-counter:hover svg {
  transform: scale(1.3);
}
.btn-counter:hover {
  background-color: #ccc;
}

</style>
